import { Actor } from '../actor';
import { PlayerKind } from './kind';
export class Player extends Actor {
    constructor(kind) {
        super(kind);
    }
}
Player.default = {
    ch: '@',
    fg: 'white',
    name: 'You',
};
export function make(options = {}) {
    const kind = new PlayerKind(options);
    return new Player(kind);
}
