import * as GWU from 'gw-utils';
import * as Entity from '../entity';
import * as Flags from '../flags';
import { Item } from './item';
export class ItemKind extends Entity.EntityKind {
    constructor(config) {
        super(config);
        this.flags = {
            item: Flags.Item.DEFAULT,
            entity: Flags.Entity.DEFAULT_ACTOR,
        };
        this.actions = {};
        if (config.flags) {
            this.flags.item = GWU.flag.from(Flags.Item, this.flags.item, config.flags);
            this.flags.entity = GWU.flag.from(Flags.Entity, this.flags.entity, config.flags);
        }
        if (config.actions) {
            Object.entries(config.actions).forEach(([key, value]) => {
                this.actions[key] = value;
            });
        }
    }
    make(options) {
        const item = new Item(this);
        this.init(item, options);
        return item;
    }
    init(item, options = {}) {
        super.init(item, options);
        Object.assign(item.flags, this.flags);
        item.quantity = options.quantity || 1;
    }
}
